@import '../../styles/CommonStyles/Colors/Colors.scss';

.info-link__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 15px;
    cursor: pointer;
    border-radius: 50px;

    .info-link__text {
        max-width: 120px;
        max-height: 28px;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 23px;
        line-height: 122%;
        color: $whiteColor;
    }

    .info-link__arrow {
        max-width: 24px;
        max-height: 26px;
        
        img {
            margin-top: 21%;
            color: $whiteColor;
            z-index: 0;
        }
    }

}


@media (max-width: 780px) {
    .info-link__container {
        
        .info-link__text {
            font-size: 20px;
        }
    }
}

@media (max-width: 360px) {
    .info-link__container {
        .info-link__text {
            max-width: 84px;
            max-height: 23px;
            font-size: 15px;
            line-height: 142%;
        }

        .info-link__arrow {

            img {
                left: 1px;
                top: -8px;
            }
        }
    }
}