.wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px;
    gap: 135px;

    margin: 0 auto;
    overflow-x: hidden;
}

@media (max-width: 1920px) {
    .wrapper {
        max-width: 100%;
    }
}

@media (max-width: 1024px) {
    .wrapper {
        max-width: 100%;
    }
}        


@media (max-width: 360px) {
    .wrapper {
        max-width: 360px;
        gap: 75px;
    }
}