@import '../../styles/CommonStyles/Colors/Colors.scss';
@import '../../styles/CommonStyles/Sections/Sections.css';

.form-section {
    gap: 100px;

    .form-section__header {
        gap: 30px;

        .form-section__header__subtitle {
            max-width: 458px;
            text-align: center;
            color: $greyColor;
        }
    }

    .form-section__content-form {
        gap: 33px;
        background: $whiteColor;
        border-radius: 50px;
        min-height: 986px;

        .form-cont {
            display: flex;
            max-width: 1170px;
            flex-wrap: wrap;
            justify-content: center;
            gap: 33px;
            margin-top: 77px;

        .form-section__input-name {
            color: $blackColor;
        }

        .form-section__input-fields-container {
            max-height: 104px;
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            gap: 30px;

            .form-section__input-field-container {
                max-width: 500px;
                max-height: 104px;
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 8px;

                position: relative;

                input[type=date]::-webkit-calendar-picker-indicator {
                    opacity: 0;
                }

                input[type=date]::-webkit-datetime-edit { 
                    color: $greyColor;
                    font-size: 25px;
                    font-weight: 300;
                }

                .select-wrapper::before {
                    content: url('./FormPng/Rectangle.png');
                    position: absolute;
                    top: 55%;
                    right: 25px;
                    display: block;
                    width: 20px;
                    height: 100%;
                    pointer-events: none;
                }

                .form-section__select-field {
                    min-width: 500px;
                    max-width: 500px;
                    min-height: 68px;
                    background: $beigeColor;
                    border: 1px solid $lightBeigeColor;
                    border-radius: 10px;
                    gap: 10px;
                    padding: 20px;
                    cursor: pointer;

                    -webkit-appearance: none;
                    appearance: none;
                }

                @media (min-width: 361px) {
                    .form-section__select-field:hover {
                        background: $lightBeigeColor;
                    }
                }

                .first-date-field {
                    color: $beigeColor;
                }

                .second-date-field::-webkit-calendar-picker-indicator {
                    opacity: 0;
                }

                .form-section__input-field {
                    min-width: 500px;
                    max-width: 500px;
                    min-height: 68px;
                    box-sizing: border-box;
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    padding: 20px;
                    gap: 10px;
                    background: $beigeColor;
                    border: 1px solid $lightBeigeColor;
                    border-radius: 10px;

                    cursor: pointer;
                    font-size: 20px;
                    -webkit-appearance: none;
                }

                @media (min-width: 361px) {

                    .form-section__input-field:focus{
                        border: 1px solid $greyColor;
                        background: $lightBeigeColor;
                    }

                    .first-date-field:hover {
                        color: $lightBeigeColor;
                    }

                    .first-date-field:focus {
                        color: $lightBeigeColor;
                    }
    
                    .form-section__input-field:hover {
                        background: $lightBeigeColor;
                    }
                }

                .form-section__input-field::placeholder {
                    color: $greyColor;
                }

                input[type="date"]:not(.has-value):before{
                    color: $greyColor;
                    content: attr(placeholder);
                }
            }
        }
    }

        .form-section__input-comment-container {
            max-width: 1030px;
            max-height: 188px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: 8px;
            margin-left: 70px;

            .form-section__input-name {
                color: $blackColor;
            }

            .input-comment {
                resize: none;
            }

            @media (min-width: 361px) {
                .form-section__input-field-comment:focus {
                    border: 1px solid $greyColor;
                    background: $lightBeigeColor;
                }

                .form-section__input-field-comment:hover {
                    background: $lightBeigeColor;
                }
            }

            .form-section__input-field-comment {
                max-width: 1030px;
                min-width: 1030px;
                min-height: 152px;
                box-sizing: border-box;
                display: flex;
                flex-direction: row;
                align-items: center;
                padding: 20px;
                gap: 10px;
                background: $beigeColor;
                border: 1px solid $lightBeigeColor;
                border-radius: 10px;
                font-size: 20px;
                cursor: pointer;
            }
        }

        .form-section__age-check {
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            gap: 30px;
            margin-top: 6px;
            margin-left: 70px;

            .form-section__age-check__input-container {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: 12px;
                margin-top: 0px;

                .form-section__input-name {
                    color: $blackColor;
                }

                .age-check__radio-buttons-container {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 31px;

                    .age-check__radio-button-container {
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        gap: 11px;

                        .age-check__radio-button {
                            display: none;
                        }

                        .radio__border {
                            min-width: 21px;
                            min-height: 21px;
                            border: 2px solid $blackColor;
                            border-radius: 50%;
                            position: relative;
                        }

                        .radio__border::before {
                            content: "";
                            position: absolute;
                            top: 48%;
                            left: 48%;
                            transform: translate(-50%, -50%);
                            display: block;
                            min-width: 12px;
                            min-height: 12px;
                            background-color: $blackColor;
                            border-radius: 50%;
                            transition: .2s;
                            z-index: -1;
                        }

                        .age-check__radio-button:checked + .radio__border::before {
                            z-index: 1;
                        }
                    }
                }
            }
        }

        .form-section__agreement-container {
            margin-left: 70px;

            .form-section__accept {
                font-family: 'Roboto';
                font-style: normal;
                font-weight: 400;
                font-size: 17px;
                line-height: 142%;
                letter-spacing: -0.01em;
                color: $greyColor;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                gap: 19px;
                
                .form-section__agreement-radio-button {
                    display: none;
                }

                .agreement-text {


                    .form-section__agreement-text-link {
                        color: $blackColor;
                        text-decoration: none;
                        cursor: pointer;
                        margin-left: 1px;
                        font-size: 17px;
                        background: inherit;
                    }
                }

                .checkbox__border {
                    display: inline-block;
                    min-width: 20px;
                    min-height: 20px;
                    border: 2px solid $blackColor;
                    border-radius: 20%;
                    position: relative;
                }

                .checkbox__border::before {
                    content: '';
                    position: absolute;
                    left: -1.3px;
                    top: 48%;
                    min-height: 29%;
                    min-width: 2px;
                    background-color: $blackColor;
                    transform: translateX(9.45px) rotate(-45deg);
                    transform-origin: left bottom;
                    opacity: 0;
                    transition: .2s;
                }

                .checkbox__border::after {
                    position: absolute;
                    left: 9%;
                    bottom: 19%;
                    min-height: 2px;
                    min-width: 68%;
                    background-color: $blackColor;
                    content: "";
                    transform: translateX(7.1px) rotate(-46deg);
                    transform-origin: left bottom;
                    opacity: 0;
                    transition: .2s;
                }

                .form-section__agreement-radio-button:checked + .checkbox__border::before,  
                .form-section__agreement-radio-button:checked + .checkbox__border::after {
                    opacity: 1;
                }

                @media (min-width: 361px) {

                    .form-section__agreement-text-link:hover {
                        text-decoration: underline;
                    }
    
                    .form-section__agreement-text-link:active {
                        color: $darkGreen;
                    }
                }
            }
        }

        .form-section__buttons-container {
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            gap: 30px;
            margin-left: 70px;

            .form-section__submit-button {
                min-width: 235px;
                min-height: 58px;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                gap: 10px;
                background: $darkGreen;
                border-radius: 10px;
                cursor: pointer;

                .form-section__submit-button-text{
                    color: $whiteColor;
                    background-color: inherit;
                    min-width: 235px;
                    min-height: 58px;
                    border-radius: 10px;
                }
            }

            @media (min-width: 361px) {

                .form-section__submit-button:hover {
                    background: $greenColor;
                }
    
                .form-section__submit-button:active {
                    background: $oliveGreenColor;
                }
            }

            .form-section__reset-button {
                min-width: 235px;
                min-height: 58px;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                gap: 10px;
                background: $lightGreenColor;
                border-radius: 10px;
                cursor: pointer;

                .form-section__reset-button-text {
                    color: $darkGreen;
                    min-width: 235px;
                    min-height: 58px;
                    background-color: inherit;
                    border-radius: 10px;
                }
            }

            @media (min-width: 361px) {

                .form-section__reset-button:hover {
                    background: $whiteSmoke;
                }
    
                .form-section__reset-button:active {
                    background: $gainsboro;
                }
            }

        }
    }
}

