.header {
  display: flex;
  flex-direction: column;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 39.9%), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url("./HeaderPNG/header.png");
  background-size: cover;
  background-position: center;
  width: 100%;
}
.header .header__navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0px;
  min-height: 88px;
}
.header .header__navbar .navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  min-height: 30.24px;
  gap: 180px;
}
.header .header__navbar .navbar .navbar__logo {
  min-width: 181.84px;
  max-width: 181.84;
  min-height: 30.24px;
}
.header .header__navbar .navbar .navbar__logo .YourTourLogo {
  left: 376px;
  top: 28.88px;
}
.header .header__navbar .navbar .navbar__menu {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 50px;
  min-height: 28px;
}
.header .header__navbar .navbar .navbar__menu .menu__item {
  min-height: 28px;
  color: #FFFFFF;
  white-space: nowrap;
  cursor: pointer;
}
.header .header__navbar .navbar .navbar__menu .menu__item:hover {
  text-decoration-line: underline;
  text-decoration-thickness: 1px;
  text-underline-offset: 7px;
}
.header .header__navbar .navbar .telephone {
  max-width: 155px;
  min-height: 28px;
  color: #FFFFFF;
  white-space: nowrap;
}
.header .header__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
  min-height: 743px;
}
.header .header__content .header__content__title {
  max-width: 768px;
  min-height: 315px;
  font-style: normal;
  font-weight: 700;
  font-size: 96px;
  line-height: 109%;
  text-align: center;
  letter-spacing: 0.02em;
  color: #FFFFFF;
}
.header .header__content .header__content__subtitle {
  max-width: 562px;
  text-align: center;
  color: #FFFFFF;
}
@media (min-width: 361px) {
  .header .header__content .header__content__button:hover {
    background: #EFA501;
  }
  .header .header__content .header__content__button:hover .find-tour-btn {
    color: #FFFFFF;
  }
  .header .header__content .header__content__button:active {
    background: #F8E6BE;
  }
  .header .header__content .header__content__button:active .find-tour-btn {
    color: #1B1F2B;
  }
}
.header .header__content .header__content__button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 5px 20px;
  gap: 10px;
  background: #FFFFFF;
  border-radius: 10px;
  cursor: pointer;
}
.header .header__content .header__content__button .find-tour-btn {
  max-width: 78px;
  min-height: 24px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 142%;
  letter-spacing: -0.01em;
  color: #1B1F2B;
}

@media (max-width: 1175px) {
  .header .header__navbar .navbar {
    gap: 60px;
  }
}
@media (max-width: 1024px) {
  .header .header__navbar {
    max-width: 100%;
  }
  .header .header__navbar .navbar {
    max-width: 100%;
    gap: calc(30px + 5.37109375vw);
  }
  .header .header__navbar .navbar .navbar__menu {
    gap: calc(10px + 1.953125vw);
  }
  .header .header__content .header__content__subtitle {
    max-width: 442px;
  }
}
@media (max-width: 990px) and (min-width: 781px) {
  .header .header__navbar .navbar {
    gap: calc(-22px + 5.37109375vw);
  }
}
@media (max-width: 780px) {
  .header .header__navbar .navbar {
    min-width: 90%;
  }
  .header .header__navbar .navbar .navbar__menu {
    display: none;
  }
  .header .header__content .header__content__title {
    font-size: 80px;
    max-width: 540px;
    text-align: center;
  }
}
@media (max-width: 576px) {
  .header .header__navbar .navbar {
    justify-content: space-around;
  }
  .header .header__navbar .navbar .telephone {
    max-width: 124px;
    min-height: 23px;
    font-size: 16px;
  }
  .header .header__navbar .navbar .navbar__logo {
    min-width: 124px;
    max-width: 124px;
  }
  .header .header__content {
    min-height: 550px;
  }
  .header .header__content .header__content__title {
    font-size: 50px;
    min-height: 190px;
  }
  .header .header__content .header__content__subtitle {
    font-size: 18px;
    max-width: 350px;
  }
}
@media (max-width: 360px) {
  .header {
    max-height: 486px;
  }
  .header .header__content {
    min-width: 360px;
    min-height: 488px;
    gap: 40px;
    margin-top: -47px;
    margin-left: -4px;
  }
  .header .header__content .header__content__title {
    font-size: 34px;
    line-height: 112%;
    max-width: 360px;
    min-height: 114px;
  }
  .header .header__content .header__content__subtitle {
    max-width: 314px;
    font-size: 16px;
  }
  .header .header__content .header__content__button .find-tour-btn {
    font-size: 16px;
    min-height: 19px;
  }
  .header .header__navbar {
    max-width: 360px;
    margin-left: 2px;
  }
  .header .header__navbar .navbar {
    max-width: 320px;
    gap: 33px;
    justify-content: space-between;
  }
  .header .header__navbar .navbar .navbar__menu {
    display: none;
  }
  .header .header__navbar .navbar .navbar__logo {
    min-width: 124px;
  }
  .header .header__navbar .navbar .navbar__logo .YourTourLogo {
    max-width: 133px;
  }
  .header .header__navbar .navbar .telephone {
    max-width: 124px;
    min-height: 23px;
    font-size: 16px;
  }
}/*# sourceMappingURL=Header.css.map */